body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header-section{
    z-index: 10;
    background: #b403af;
    position: fixed;
    width: 100vw;
    height: 8vh;
    opacity: 0.2;
    -webkit-transition: 1s;
    transition: 1s;
    -webkit-transition-timing-function: ease-in;
            transition-timing-function: ease-in;

    display: -webkit-flex;

    display: flex;
    -webkit-flex-flow: row;
            flex-flow: row;
}

.header-section:hover{
    opacity: 1;
}
.header-section__nav-toogle{
    position: relative;
    top:1vh;
    left:1vh;
    height: 6vh;
    width: 6vh;
    cursor: pointer;
    
}
.bar1, .bar2, .bar3, .bar4{
    background: #000000;
    position: absolute;
    top:40%;
    width: 100%;
    height:16%;
    border-radius: 1rem;
    -webkit-transition: 1s;
    transition: 1s;
    -webkit-transition-timing-function: ease-in;
            transition-timing-function: ease-in;
    width:57%;
}
.bar2, .bar4{
    left:36%;
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
}
.bar1, .bar3{
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}
.bar3{
    left:36%;
}
.bar4{
    left:72%;
}
.header-section:hover .header-section__nav-toogle .bar1{
    top:20%;
    z-index: 11;
    -webkit-transform: rotate(750deg);
            transform: rotate(750deg);    
}

.header-section:hover .header-section__nav-toogle .bar2{
    top:20%;
    z-index:12;
    -webkit-transform: rotate(-750deg);
            transform: rotate(-750deg);
    left:36%;
}

.header-section:hover .header-section__nav-toogle .bar3{
    z-index: 13;
    top: 60%;
    -webkit-transform: rotate(750deg);
            transform: rotate(750deg);
    left:0%;
}

.header-section:hover .header-section__nav-toogle .bar4{
    z-index: 14;
    top :60%;
    -webkit-transform: rotate(-750deg);
            transform: rotate(-750deg);
    left:36%;
}


.nav-bar-section{
    top:8vh;
    height: 92vh;
    width:100vw;
    position: fixed;
    display: grid;
    grid-template-columns: auto 1fr;
    -webkit-transition: 1s;
    transition: 1s; 
    overflow: hidden;
}
.nav-bar-section__navbar{
    z-index: 10;
    background: #000000;
    width: 300px;
    position: relative;
    display: grid;
    grid-template-columns: 1fr;
    -webkit-align-items: center;
            align-items: center;
    justify-items: center;
    color:#ffffff;
    
}

.nav-bar-section__non-navbar{
    position: relative;
    height: 100%;
    width: 100%;
    background: #43d2de;
    opacity: 0.5;
    cursor: pointer;
}
.nav-bar-section__navbar nav{
    cursor: pointer;
    height:100%;
    width: 100%;
    border: 1px #ffffff;    
    -webkit-align-items: center;    
            align-items: center;
    font-weight: 700;
    font-size: 16px; 
    display: -webkit-flex; 
    display: flex;
    padding-left: 50px;
    -webkit-flex-direction: row;
            flex-direction: row;
    overflow: hidden;
}
.nav-bar-section__navbar nav:hover{
    color:#43d2de;
}
.nav-bar-section--closing{
    width: 0px;
}
.home-section{
    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(200px,1fr));
    height: 100vh;
}

.about-section{
    height: 100vh;
    background: #323232;
}
.experience-section{
    height: 100vh;
    background: #000000;
}
html {
  scroll-behavior: smooth;
}
